* {
  padding:0;
  margin:0;
  box-sizing:border-box;
}
body {
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
background:#f1f5f8;
color:#222;
}
h1 {
  color:red;
}
.bookrate{
  width:90vw;
  max-width:1170px;
  margin:5rem auto;
  display:grid;
  gap:2rem;
}
@media screen and (min-width:768px){
  .bookrate {
    grid-template-columns: repeat(3, 1fr);
  }
}
.book {
  background:#fff;
  border-radius:1rem;
  padding:1rem 2rem;
}

.book h1 {
  margin-top: 0.5rem;
}